.calc-app {
  margin: 0 auto;
  margin-top: 40px;
  width: 256px;
  text-align: center;
  border: 10px black solid;
}

.keypad {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-auto-rows: minmax(60px, auto);
}

input[type="text"] {
  height: 60px;
  width: 250px;
  border: 5px solid balck;
  border-radius:5px;
  background-color: papayawhip;
}

#clear {
  grid-column: 1/3;
  grid-row: 1;
  color: rgb(44, 43, 43);
  background-color: rgb(164, 234, 247);
  border-radius: 20px;

}

#result {
  grid-column: 3/5;
  grid-row: 5;
  color: rgb(12, 12, 12);
  background-color: green;
  border: 2px solid black;
  border-radius: 1px;
  
}

#backspace {
  color: rgb(12, 12, 12);
  background-color: rgba(226, 39, 39, 0.829);
  border-radius: 100px;
}